.media__caption {
    position: absolute;
    bottom: 1.5rem;
    left: 1rem;
    transition: transform 350ms $transition-bezier, opacity 350ms ease;
    pointer-events: none;
    z-index: 2;
}

.media__icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: scale(0) translate(-50%, -50%);
    transform-origin: 0 0;
    opacity: 0;
    transition: $transition-base;
}

.media__container {
    position: relative;
    overflow: hidden;
    width: 100%;
    z-index: 1;

    + .img-fluid {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
}

.media__video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
}

.media--clients {
    justify-content: center;

    img {
        width: 100%;
        max-width: 100px;
        opacity: 0.5;

        @include media-breakpoint-up(md) {
            max-width: 150px;
        }
    }
}
